@font-face {
  font-family: 'Raleway';
  src: url("../public/fonts/Raleway/Raleway-VariableFont_wght.ttf");
}

@font-face {
  font-family: 'Yatra One';
  src: url("../public/fonts/Yatra_One/YatraOne-Regular.ttf");
}

*{
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  margin: 0;
  padding: 0;
  color: #fff !important ;
}

.Map, .App {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.Interface {
  aspect-ratio: 9 / 16;
  height: 100%;

  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}


.debug {
  z-index: 100;
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 15px 20px;
}

.debug p {
  margin: 5px;
}

.App {
  position: relative;
}

.content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.div_test {
  color : black ; 
  font-size: 1vh;
  display: flex;
  flex-direction: row;
}

h2 {
  margin: 0;
  font-size: 5vh;
  font-family: 'Yatra One', cursive;
}

h3 {
  font-weight: bold;
}

p {
  font-size: 1.2vh;
}


.go_map {
  width: 9vh !important;
} 


/* ---------------------------- PAGE POINT ---------------------------- */
.pointPage {
  width: 75%;
  height: 80%;
  background : rgba(27, 27, 27, 0.8) ;
  padding: 3vh;
  margin: auto;
  position: relative;
}

.cheminHeader {
  text-align: center;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-100%);
  background-color: #FF6B65;
  font-weight: bold;
  font-size: 15px;
  padding: 5px 0;
}

.pointPage h2 {
  font-size: 3.8vh;
}

.pointPage h3 {
  font-size: 1.5vh;
  margin-top: -0.8vh;
}

video {
  width: 100%;
  margin: 2vh 0;
}

.contenu{
  margin: 0 5%;
  overflow-y: scroll;
  margin: 2vh 0;
  scrollbar-color: #818181;
  scrollbar-width: thin;
  padding-right: 5px;
  padding-bottom: 100px;
  max-height: 30%;
}

.contenu::-webkit-scrollbar {
  width: 5px;
  height: 8px;
}

.contenu::-webkit-scrollbar-thumb {
  background: #818181;
  -webkit-border-radius: 1ex;
  -webkit-box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.75);
}

.btn {
  background: none;
  border: none;
  margin: 0 1vh ;
}

.links {
  display: flex;
  justify-content: center ;
  padding: 0 4%;
}

.links .btn {
  margin: 1vh auto;
  width: 13vh;
}

.links img{
  width: 100%;
}

.buttons {
  display: flex;
  justify-content: center ;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgb(27,27,27);
  background: linear-gradient(0deg, rgba(27,27,27,1) 0%, rgba(27,27,27,0.6) 100%);
  padding: 10px 0;
}

.buttons img {
  margin: 0 1vh ; 
  width: 6vh ;
}

.sound {
  margin: 1vh 2vh;
  display: flex;
  flex-direction: column;
}

.sound div{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.proverbe{
  width: 85%;
  margin: 0.7vh 0.6vh 0.7vh 0;
  font-style: italic;
}

.sound img{
  width: 1.8vh;
}

/* ---------------------------- A PROPOS ---------------------------- */

.aPropos{
  width: 75%;
  height: 70%;
  background : rgba(27, 27, 27, 0.8);
  padding: 5vh;
  margin: auto;
}

.scroll{
  max-height: 50vh;
  overflow-y: scroll;
  margin: 1vh 0 ;
  scrollbar-color: #818181;
  scrollbar-width: thin;
  padding-right: 5px;
}

.scroll::-webkit-scrollbar {
  width: 5px;
  height: 8px;
}

.scroll::-webkit-scrollbar-thumb {
  background: #818181;
  -webkit-border-radius: 1ex;
  -webkit-box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.75);
}

.scroll *{
  margin: 1.5vh 0;
}

.aPropos h2{
  font-size: 3vh;
}

.aPropos h3{
  font-size: 1.5vh;
}

.article{
  font-size: 1.3vh;
  margin-bottom: 0;
  text-align: left;
  font-style: italic;
}

.auteur{  
  font-size: 1.3vh;
  text-align: right;
  margin: 0.4vh 0;
  margin-top: 15px;
}

.provenance{
  font-size: 1.3vh;
  margin-top: 0;
  margin-bottom: 30px;
  text-align: right;
}

.mcc_div{
  display: flex;
  margin: 2vh 0;
  width: 100%;
  flex-direction: column;
  margin-top: 50px;
}

.mcc{
  width: 100% !important;
  margin: auto;
}

figcaption{
  font-size: 1.3vh;
  text-align: center;
}

span {
  font-weight: bold;
}

.aPropos button{
  width: 100%;
  margin: 1vh auto;
  cursor: pointer;
}

/* ---------------------------- MENU ---------------------------- */

.Menu{
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 100;
  left: 0;
  top: 0;
  pointer-events: none;
}

.menuContent {
  pointer-events: auto;
  box-sizing: border-box;
  position: absolute;
  right: 0;
  top: 0; 
  width: 60%;
  height: 100%;
  padding: 15vh 3vh;
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: left;
  border-left: 1.5rem solid #FF6B65;
  background-color: rgba(27, 27, 27, 0.8) ;
  transition: transform 0.5s ease-in-out;
  transform: translateX(100%);
}

.Menu.open .menuContent{
  transform: translateX(0);
}

.titlesChemin{ 
  margin-bottom: 5vh;
}

.titlesChemin h2{
  margin: 0;
  font-size: 5vh;
}

.titlesChemin h3{
  margin: 0;
  font-size: 3.5vh;
  font-family: 'Yatra One', cursive;
}

.cheminLink{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 2vh 0;
  cursor: pointer;
}

.cheminLink img{
  max-height: 75px;
  max-width: 70px;
  margin-right: 2vh;
}

.cheminLink p{
  font-size: 1.8vh;
}

.menuButton {
  position: absolute;
  left: -100px;
  top: -80px; 
  padding: 100px 20px;
  cursor: pointer;
  transition: transform 0.5s ease-in-out;
  transform: rotate(0);
}

.menuButton.menuOpen {
  transform: rotate(180deg);
}

.menuButton img{
  width: 4vh;
}

/* ---------------------------- Footer ---------------------------- */

.Footer {
  z-index: 999;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40px;
  background : rgba(27, 27, 27, 0.2);
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}

.Footer .aProposLink {
  height: 100%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Footer  .langues img {
  height: 25%;
  cursor: pointer;
}